import { useState } from "react";
import Select from 'react-select';

const TestList = [
  { value: '1', label: '1' },
  { value: '2', label: '2' }
]

export default function TestSlct(props) {

  const [testText, setTestText] = useState('')

  const test = (e) => {
    console.log(e.value)
    setTestText(e.value)
  }

  return (
    <>
      <Select options={TestList} onChange={(e) => test(e)} />
      {testText}
    </>

  )
}
