import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Button from "@material-ui/core/Button";
import ModalActSlct from "./ModalActSlct";



export default function DataGridAct(props) {
  // console.log(props)

  const test = (t, e) => {
    console.log(props.SlctMain, t, e)
    //props.handleClose()
    // props.SprtSlctOn(props.SlctMain, e)
  }



  const columns = [
    // {
    //   field: 'id',
    //   headerName: '',
    //   type: 'number',
    //   width: 60,
    // },
    {
      field: 'turn',
      headerName: '',
      type: 'number',
      width: 60,
      headerAlign: 'center',
    },
    {
      field: 'order',
      headerName: '',
      type: 'number',
      width: 60,
      headerAlign: 'center',
    },
    
    {
      field: 'icon',
      headerName: '',
      width: 40,
      headerAlign: 'center',
      align: 'center',
      renderCell: (params) => (
        <ModalActSlct
          SlctMain={props.SlctMain}
          Main={props.Main}
          index={params.row.id}
          ActNam={params.row.name}
          ActList={props.ActList}
          ActSlctOn={props.ActSlctOn}
          handleClose={props.handleClose}
        />
      )
    }, {
      field: 'name',
      headerName: '',
      width: 160,
    }, {
      field: 'Dmg',
      headerName: 'CR',
      type: 'number',
      width: 80,
      align: 'right',

    }, {
      field: 'DmgEx',
      headerName: '',
      type: 'number',
      width: 80,
      align: 'right',
    }, {
      field: 'DmgCr',
      headerName: 'CR',
      type: 'number',

      width: 80,
      align: 'right',
    }, {
      field: 'chart',
      headerName: '',
      type: 'string',
      width: 240,
      headerAlign: 'left',
      renderCell: (params) => (
        <>
          <img src={`${process.env.PUBLIC_URL}/Dmg.png`} style={{ width: params.row.Dmg / 1000, height: 100 }} alt={''} />
          <img src={`${process.env.PUBLIC_URL}/DmgEx.png`} style={{ width: (params.row.DmgEx - params.row.Dmg) / 1000, height: 100 }} alt={''} />
          <img src={`${process.env.PUBLIC_URL}/DmgCr.png`} style={{ width: (params.row.DmgCr - params.row.DmgEx) / 1000, height: 100 }} alt={''} />
        </>
      )

    },

  ];






  const rows = []



  for (let i = 0; i < props.Act.length; i++) {
    rows.push({
      id: i,
      order: i+1,
      turn: props.Act[i].t+1,
      icon: props.Act[i].Nam,
      name: props.Act[i].Nam,
      Dmg: props.Act[i].UnitDmg,
      DmgEx: props.Act[i].UnitDmgEx,
      DmgCr: props.Act[i].UnitDmgCr,
      chart: '',
    })
  }


  return (
    <Box sx={{ height: 400, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        density='compact'//
        autoHeight//
        // pageSize={5}
        // rowsPerPageOptions={[5]}
        // checkboxSelection
        disableSelectionOnClick
        experimentalFeatures={{ newEditingApi: true }}
        showColumnRightBorder // 
        showCellRightBorder   // 
        hideFooter//
        hideFooterSelectedRowCount//
        hideFooterPagination//

      />
    </Box>
  );
}